import React from 'react'
import { EventEmitter } from 'events'

let CHANGE_EVENT = "change", USER_EVENT = "user";

class Store extends EventEmitter {

  constructor() {
    super();
    console.log("Store Constructor")
    this.state = { user: null, images: null };
  }

  user(u) {
    //this.setState({ user: user });
    if (u) {
      this.state.user = user;
      this.emitChange(USER_EVENT);
    } else {
      return this.state.user;
    }
  }

  getState() {
    return this.state;
  }

  emitChange(eventName) {
    console.log("Emiting change", eventName);
    this.emit(eventName ? eventName : CHANGE_EVENT);
  }

  addListener(callback, eventName) {
    console.log("Listening change", eventName);
    this.on(eventName ? eventName : CHANGE_EVENT, callback);
  }

  removeListener(callback, eventName) {
    console.log("UnListening change", eventName);
    this.removeListener(eventName ? eventName : CHANGE_EVENT, callback);
  }
}

Store.dispatchToken = null;
let storeSingle = new Store();

export default storeSingle;


