import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Spinner from './Spinner'

class Instagram extends Component {
  state = {
    user: null,
    pages: null,
    working: false
  }

  componentDidMount() {
    this.status()
  }

  status = () => {
    console.log("Instagram Status")

  }

  busy = (isBusy) => {
    this.setState({ working: (isBusy === false ? false : true) });
  }

  login = () => {
    var that = this;
    console.log("works?")
    this.busy();
    // client side access_token flow (implicit)
    IG.login(function (response) {
      if (response.session) {
        // user is logged in
      }
    }, { scope: ['comments', 'likes'] });
  }

  extendToken = (token) => {
    var that = this;
    fetch('http://onedestapidev.azurewebsites.net/configuration/extendAccessToken?appId=749679035174071&accessToken=' + token, {})
      .then(function (response) {
        return response.json();
      }).then(function (data) {
        that.setState({ extendedToken: data });
        that.pages(data);
      }).catch(function (error) {
        console.log('request failed', error)
      });
  }
  signup = (name, token, pageId, fanCount) => {
    let data = {
      "Name": "string",
      "PageId": pageId,
      "PageToken": "string",
      "PageType": 0,
      "FanCount": fanCount
    };
    fetch('http://46.45.187.173/LikeometerTest/user/organization', {
      method: "POST",
      body: JSON.stringify(data)
    })
      .then(function (data) {
        console.log('request succeeded with JSON response', data)
      }).catch(function (error) {
        console.log('request failed', error)
      });
  }

  logout = () => {
    FB.logout(function (response) { });
  }

  pages = (longToken) => {
    var that = this;
    FB.api('me/accounts', 'get', { access_token: longToken, fields: 'picture,fan_count,name,category,access_token' }, function (accountsResponse) {
      if (accountsResponse.data) {
        that.setState({ pages: accountsResponse.data });
        console.log(that.state.pages);
      }
    });
  }

  render() {
    const { pages, working } = this.state;
    return (
      <div style={{ textAlign: "center" }} >
        <button className="btn btn-primary btn-radius ins-connect" onClick={this.login} >
          <i className="fa fa-instagram"></i>&nbsp;&nbsp;&nbsp;&nbsp;CONNECT WITH INSTAGRAM</button> <br />
        {working && (<Spinner />)}
      </div>
    )
  }
}
export default Instagram



